import React, { useState } from 'react';
import LoginForm from '../src/pages/LoginForm';
import RegistrationForm from '../src/pages/RegistrationForm';
import TransactionList from '../src/pages/TransactionList';
import ForgotPasswordForm from './pages/ForgotPasswordForm';
import './App.css';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';

const App = () => {
  const [username, setUsername] = useState('');
  const [isLoggedIn, setIsLoggedIn] = useState(false);


  const handleLogin = (username) => {
    setUsername(username);
  };

  const handleRegister = (username) => {
    setUsername(username);
  };

  const handleLogout = () => {
    setIsLoggedIn(false);
  };
  

 
  return (
    <Router>
      <div className="app-container">
        <Routes>
          <Route path="/login" element={<LoginForm onLogin={handleLogin} />} />
          <Route path="/register" element={<RegistrationForm onRegister={handleRegister} />} />
          <Route path="/forgot-password" element={<ForgotPasswordForm />} />
          <Route path="/home" element={<TransactionList username={username} logout={handleLogout} />} />
          <Route path="/*" element={<Navigate to="/login" />} />
        </Routes>
      </div>
    </Router>
  );
};

export default App;

